 <template>
  <div id="add-blog">
      <h2>添加博客</h2>
      <form action="" v-if="!submited">
          <label for="">博客标题</label>
          <input type="text" v-model="blog.title" required />
          <label for="">博客内容</label>
          <textarea name="" id="" cols="30" rows="10" v-model="blog.content"></textarea>
          <div id="checkboxes">
              <label for="">Vue.js</label>
              <input type="checkbox" name="" id="" value="Vue.js" v-model="blog.categories">
              <label for="">Node.js</label>
              <input type="checkbox" name="" id="" value="Node.js" v-model="blog.categories">
              <label for="">React.js</label>
              <input type="checkbox" name="" id="" value="React.js" v-model="blog.categories">
              <label for="">Angular4</label>
              <input type="checkbox" name="" id="" value="Angular4" v-model="blog.categories">
          </div>
          <label for="作者："></label>
          <select name="" id="" v-model="blog.author">
              <option v-for="(author, index) in authors" :key="index">{{author}}</option>
          </select>
          <button v-on:click.prevent="post">添加博客</button>
      </form>
      <div v-if="submited">
        <h3>您的博客发布成功！</h3>
      </div>
      <div id="preview">
          <h3>博客总览</h3>
          <p>博客标题：{{blog.title}}</p>
          <p>{{blog.content}}</p>
          <ul>
              <li v-for="(category, index) in blog.categories" :key="index">{{category}}</li>
          </ul>
          <p>作者：{{blog.author}}</p>
      </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog: {
        title: '',
        content: '',
        categories: [],
        author: ''
      },
      authors: ['harry', 'henry', 'helen'],
      submited: false
    }
  },
  methods: {
    post: function () {
      this.$http.post('https://vue-demo-g.firebaseio.com/posts.json', this.blog).then(function (data) {
        console.log(data)
        this.submited = true
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}

#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label {
  display: block;
  margin: 20px 0 10px;
}

input[type= "text"], textarea, select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 200px;
}

#checkboxes label {
  display: inline-block;
  margin-top: 0;
}
#checkboxes input {
  display: inline-block;
  margin-top: 20px 0;
}

button {
  display: block;
  margin: 20px 0;
  background:crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 20px 0;
}
h3 {
  margin-top: 10px;
}
</style>



