<template>
  <div v-theme:column id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索" name="" id="">
    <div class="single-blog" v-for="(blog, index) in filteredBlogs" :key="index">
      <router-link v-bind:to="'/blog/' + blog.id"><h2 v-rainbow>{{blog.title | to-uppercase}}</h2></router-link>
      <article>
        {{blog.content | snippet}}
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Show-blogs',
  data () {
    return {
      blogs: [],
      search: ''
    }
  },
  created () {
    this.$http.get('https://vue-demo-g.firebaseio.com/posts.json').then(function (data) {
      return data.json()
    }).then(function (data) {
      var blogsArray = []
      for (let key in data) {
        console.log(data[key])
        console.log(key)
        data[key].id = key
        blogsArray.push(data[key])
      }
      this.blogs = blogsArray
    })
  },
  computed: {
    filteredBlogs: function () {
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search)
      })
    }
  },
  filters: {
    toUppercase (value) {
      return value.toUpperCase()
    },
    snippet (value) {
      return value.slice(0, 100) + '...'
    }
  },
  directives: {
    'rainbow': {
      bind (el, binding, vnode) {
        el.style.color = '#' + Math.random().toString(16).slice(2, 8)
      }
    }
  }
}
</script>

<style>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}

.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
}

#show-blogs a {
  color: #444;
  text-decoration: none;
}
input[type= "text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>



